body.active-Enquiryform {
    overflow-y: hidden;
}

.btn-Enquiryform {
    padding: 10px 20px;
    display: block;
    margin: 100px auto 0;
    font-size: 18px;
}

.Enquiryform, .overlay {
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    position: fixed;
}

.overlay {
    background: rgba(49,49,49,0.8);
}
.Enquiryform-content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    line-height: 1.4;
    background: #f1f1f1;
    padding: 14px 28px;
    border-radius: 3px;
    max-width: 600px;
    min-width: 300px;
}

.close-Enquiryform {
    position: absolute;
    top: 10px;
    right: 10px;
    padding: 5px 7px;
}

fr{
    position: absolute;
    padding: 10px;
}

